<template>
  <div id="app">
    <div class="container">
      <h1>Credits (CRDS)
        <span class="subtitle">QR Code Generator</span>
      </h1>
      <form v-on:submit.prevent="generate">
        <div class="row">
          <div class="eight columns">
            <label for="address">CRDS Address :</label>
            <input v-model="address" type="text" class="u-full-width" id="address" placeholder="Enter your CRDS public address..."/>
          </div>
          <div class="four columns">
            <label for="size">Size :</label>
            <input v-model="size" type="text" class="u-full-width" id="size" value="250"/>
          </div>
        </div>

        <div class="row">
          <div class="twelve columns">
            <button type="submit" class="button-primary button-large u-full-width">Generate</button>
          </div>
        </div>

        <div v-show="generated" class="row">
          <div class="twelve columns">
            <canvas class="qr-code" id="qr"></canvas>
          </div>
        </div>
      </form>

    </div>
  </div>
</template>

<script>
import QRious from 'qrious';

function gup(name, url) {
  if (!url) url = location.href;
  name = name.replace(/[\[]/,"\\\[").replace(/[\]]/,"\\\]");
  var regexS = "[\\?&]"+name+"=([^&#]*)";
  var regex = new RegExp( regexS );
  var results = regex.exec( url );
  return results == null ? null : results[1];
}

export default {
  name: 'app',
  data() {
    return {
      address: '',
      size: 250,
      generated: false,
      standalone: false,
    }
  },
  mounted() {
    const addr = gup('addr')
    if (addr  !== null) {
      this.standalone = true;
      this.address = addr;
      this.generate();
    }
  },
  methods: {
    generate() {
      const qr = new QRious({
        element: document.getElementById('qr'),
        value: 'credits:' + this.address,
        size: this.size
      });

      this.generated = true;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Maven Pro', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 40px;
}

input, button {
  font: inherit;
}

.container {
  max-width: 640px;
}

.qr-code {
  border: solid 1px #D1D1D1;
  border-radius: 5px;
  max-width: 100%;
  padding: 10px;
}

.button-large {
  height: auto;
  padding: 10px 30px;
}

h1 .subtitle {
  font-size: 3rem;
  color: grey;
  display: block;
}
</style>



